import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from './utils/axios';

import "@/style/defaule.scss"
// import "@/style/mycss.scss"


// 按需引入ui库
import { Swiper, SwiperItem, Button, Navbar, TextArea, Tabbar, TabbarItem, Tabs, TabPane, SearchBar, Toast, Category, CategoryPane, Empty, InfiniteLoading, Sku, OverLay, ActionSheet, Popup, Dialog, AddressList, Address, Elevator, InputNumber, Price, Cell, CellGroup, BackTop, Input, Icon, Tag, Layout, Row, Col, ConfigProvider } from "@nutui/nutui"
import "@nutui/nutui/dist/style.css";

const app = createApp(App)
app
  .use(Tabbar)
  .use(ConfigProvider)
  .use(Layout)
  .use(Row)
  .use(Col)
  .use(Tag)
  .use(TabbarItem)
  .use(TextArea)
  .use(Elevator)
  .use(AddressList)
  .use(Address)
  .use(Button)
  .use(Tabs)
  .use(TabPane)
  .use(Navbar)
  .use(SearchBar)
  .use(ActionSheet)
  .use(Toast)
  .use(Category)
  .use(CategoryPane)
  .use(Dialog)
  .use(Swiper)
  .use(SwiperItem)
  .use(Empty)
  .use(InfiniteLoading)
  .use(CellGroup)
  .use(Sku)
  .use(Popup)
  .use(Input)
  .use(InputNumber)
  .use(Price)
  .use(Cell)
  .use(BackTop)
  .use(OverLay)
  .use(Icon)
  .use(store)
  .use(router)
  .mount('#app')

app.config.globalProperties.$axios = axios;

