
import { reactive, toRefs, ref, onMounted, onActivated, onUpdated } from "vue"
import * as _ from "lodash"


export default {
  name: "LoginPage",
  emits: ["loginPageClose"],
  props: {
    showLoginPage: {
      type: Boolean,
      default: false,
    },
  },
  setup(props: any, context: any) {
    const pswShow = ref(false)
    const isVisible = ref(false)
    const state = reactive({
      username: "",
      name: "",
      password: "",
    })
    onMounted(() => {})

    const pswClickRightIcon = () => {
      pswShow.value = !pswShow.value
    }
    const backBtn = () => {
      context.emit("loginPageClose")
    }
    const clickLoginBtn = _.debounce(() => {}, 200)

    return {
      ...toRefs(state),
      pswShow,
      pswClickRightIcon,
      clickLoginBtn,
      isVisible,
      backBtn,
    }
  },
}
