import { createStore } from 'vuex'

export default createStore({
  state: {
    name:'张三',
    env: 'h5',
    clientWidth:900,
  },
  getters: {
    GET_clientWidth(state) {
      return state.clientWidth
    },
    GET_ENV(state) {
      return state.env
    },
  },
  mutations: {
    SET_CLIENTWIDTH(state,data) {
      state.clientWidth = data
    },
    SET_ENV(state,data) {
      state.env = data
    },
  },
  actions: {
  },
  modules: {
  }
})
